import { useState } from "react"
import Caluculation1 from "./caluculation1"
import InputHuyo from './inputHuyo';
import InputPay from "./inputPay"
import InputIncome from "./inputOtherIncome"
import Transition from "./transition";
import { FormInput } from "./FormInput";

function ApplicationHome() {
    /*変数 */
    const [currentPage, setCurrentPage] = useState("input1")//現在のページ
    const [taxIncome, setTaxIncome] = useState('')//収入
    const [taxIncome60, setTaxIncome60] = useState('')//60歳の収入
    const [pension, setPension] = useState('')//年金の額
    const [age, setAge] = useState('')//現在の歳
    const [info, setInfo] = useState({})//<=推移ページに渡す年収や年金貯蓄が入ったオブジェクト
    const [saving, setSaving] = useState('')//現在の貯蓄額
    const [hasWife, setHasWife] = useState('')//配偶者がいるか
    const [wifeAge, setWifeAge] = useState('')//配偶者の年齢
    const [wifeTaxIncome, setWifeTaxIncome] = useState('')//配偶者の収入
    const [wifeIncome60, setWifeIncome60] = useState('')//配偶者の60歳の収入
    const [wifePension, setWifePension] = useState('')//配偶者の年金
    const [wifeInfo, setWifeInfo] = useState({})//<=推移ページに渡す配偶者の情報が入ったオブジェクト
    /*扶養、支出、その他収入のためのステート*/
    const [numberH, setNumberH] = useState([])//<=扶養の数とIDを保管する
    const [huyoList, setHuyoList] = useState([])//<=扶養の情報を保管する
    const [numberP, setNumberP] = useState([])//<=支出の数とIDを保管する
    const [payList, setPayList] = useState([])//<=支出の情報を保管する
    const [numberI, setNumberI] = useState([])//<=その他収入の数とIDを保管する
    const [incomeList, setIncomeList] = useState([])//<=その他収入の情報を保管する

    /*入力値から得られた情報の入れ箱 */
    const [currentInfo, setCurrentInfo] = useState([
        { taxIncome: 0, kyuyoIncome: 0, tedori: 0 },//<=[0]
        { kisoKojo: 0, huyoKojoSum2: 0, shakaiHoken: 0, kazeiIncome: 0 },//<=[1]
        {
            shakaiHoken: 0,//<=[2]
            seimeiHoken: 0,
            jisinKojo: 0,
            wifeKojo: 0,
            huyoKojoSum1: 0,
            kisoKojo: 0
        },
        {
            shakaiHoken: 0,//<=[3]
            seimeiHokenJ: 0,
            jisinKojoJ: 0,
            wifeKojoJ: 0,
            kisoKojoJ: 0
        }])//<=給与収入、所得税額控除のすべて、住民税控除のすべて、所得税、住民税、税合計、手取り



    /*万能ミラクルスーパーハイパーマスターレジェンドMVPホームラン王大谷翔平10刀流関数(初期値入れると手取り出してくれる) */
    const dicisionInfo = (taxIncome1, taxIncome60, pension, huyoList, who, age, year) => {
        let taxIncome = 0
        //console.log(`このとき${Number(age) + Number(year)}歳です`)
        //もし65歳以上だったら年金を返す
        if (Number(age) + Number(year) > 65) {
            return (
                [{ tedori: pension }]
            )

            console.log()
        } else {
            if (Number(age) + Number(year) < 60) {//<=60歳未満だったら普通の収入を使う
                taxIncome = taxIncome1
            } else {
                taxIncome = taxIncome60//<=60~65だったら60歳の収入を使う
            }

            let kyuyoIncome = 0//<=給与所得を入れる
            let kisoKojo = 0//<=基礎控除を入れる
            let wifeKojo = 0//<=配偶者控除
            let childrenHuyo = 0//<=子供の扶養を入れる
            let otherHuyo = 0//<=老人の扶養とその他扶養を入れる
            let huyoKojoSum1 = 0//<=子供年寄りその他<=計算例の時に使う
            let huyoKojoSum2 = 0//<=扶養控除の合計額を入れる
            let shakaiHoken = 0//<=社会保険を入れる
            let seimeiHoken = 5//<=一律5万円でいい
            let kazeiIncome = 0//<=課税所得を入れる

            //給与所得の計算
            if (taxIncome < 162.5) {
                kyuyoIncome = taxIncome * 1 - 55
            } else if (taxIncome >= 162.5 && taxIncome < 180) {
                kyuyoIncome = taxIncome * 0.6 - 10
            } else if (taxIncome >= 180 && taxIncome < 360) {
                kyuyoIncome = taxIncome * 0.7 - 8
            } else if (taxIncome >= 360 && taxIncome < 660) {
                kyuyoIncome = taxIncome * 0.8 - 44
            } else if (taxIncome >= 660 && taxIncome < 850) {
                kyuyoIncome = taxIncome * 0.9 - 110
            } else {
                kyuyoIncome = taxIncome * 1 - 195
            }

            /*基礎控除の計算 */
            if (taxIncome <= 2400) {
                kisoKojo = 48
            } else if (taxIncome > 2400 && taxIncome <= 2450) {
                kisoKojo = 32
            } else if (taxIncome > 2450 && taxIncome <= 2500) {
                kisoKojo = 16
            } else {
                kisoKojo = 0
            }

            /*扶養額の計算 */
            if (huyoList.length !== 0) {
                huyoList.forEach((data) => {
                    if (data.self === who) {
                        if (data.type === "wife") {
                            wifeKojo += 38;
                        } else if (data.type === "child") {
                            console.log(`このお子さんはこの時${Number(data.childAge) + year}歳です`)
                            if (Number(data.childAge) + year >= 16 && Number(data.childAge) + year <= 18) {
                                childrenHuyo += 38;
                            } else if (Number(data.childAge) + year >= 19 && Number(data.childAge) + year <= 22) {
                                childrenHuyo += 63;
                            } else {
                            }
                        } else if (data.type === "other") {
                            otherHuyo += 38 * Number(data.numberOfHuyo);
                        } else if (data.type === "sinior1") {
                            otherHuyo += 58 * Number(data.numberOfHuyo);
                        } else {
                            otherHuyo += 48 * Number(data.numberOfHuyo);
                        }
                    }
                })
            }


            /* 計算例で使う扶養の合計の計算*/
            huyoKojoSum1 = childrenHuyo + otherHuyo


            /*扶養控除の合計額の計算 */
            huyoKojoSum2 = kisoKojo + wifeKojo + huyoKojoSum1
            /*社会保険の計算*/
            if (taxIncome <= 399) {
                shakaiHoken = 10 + 0
            } else if (taxIncome >= 400 && taxIncome <= 499) {
                shakaiHoken = 10 + 40
            } else if (taxIncome >= 500 && taxIncome <= 599) {
                shakaiHoken = 10 + 50
            } else if (taxIncome >= 600 && taxIncome <= 699) {
                shakaiHoken = 10 + 60
            } else if (taxIncome >= 700 && taxIncome <= 799) {
                shakaiHoken = 10 + 70
            } else if (taxIncome >= 800 && taxIncome <= 899) {
                shakaiHoken = 10 + 80
            } else if (taxIncome >= 900 && taxIncome <= 999) {
                shakaiHoken = 10 + 90
            } else if (taxIncome >= 1000 && taxIncome <= 1099) {
                shakaiHoken = 10 + 100
            } else if (taxIncome >= 1100 && taxIncome <= 1199) {
                shakaiHoken = 10 + 105
            } else if (taxIncome >= 1200 && taxIncome <= 1299) {
                shakaiHoken = 10 + 110
            } else if (taxIncome >= 1300 && taxIncome <= 1399) {
                shakaiHoken = 10 + 120
            } else if (taxIncome >= 1400 && taxIncome <= 1499) {
                shakaiHoken = 10 + 125
            } else if (taxIncome >= 1500 && taxIncome <= 1599) {
                shakaiHoken = 10 + 130
            } else {
                shakaiHoken = 10 + 135
            }

            /*課税所得の計算 */
            kazeiIncome = kyuyoIncome - shakaiHoken - seimeiHoken - huyoKojoSum2

            /*ここまでで入力ページに必要なデータは作れたここからは参考計算ページ */
            /*住民税額控除のそれぞれの設定 */
            let seimeiHokenJ = seimeiHoken * 0.7 //生命保険
            let wifeKojoJ = 0;//<=配偶者控除の設定
            if (wifeKojo !== 0) {
                wifeKojoJ = 33
            } else {
                wifeKojoJ = 0
            }
            let kisoKojoJ = 0;//<=基礎控除の設定
            if (kisoKojo !== 0) {
                kisoKojoJ = kisoKojo - 5.0
            } else {
                kisoKojoJ = 0
            }

            /*住民税控除の合計 */
            let jKojoSum = shakaiHoken + seimeiHokenJ + wifeKojoJ + kisoKojoJ

            /*申告前の税額 所得税の方はkazeiIncomeでいい*/
            let kazeiIncome2 = kyuyoIncome - jKojoSum

            let sTax = 0;//<=所得税の設定
            if (kazeiIncome < 195) {
                sTax = (kazeiIncome * 0.05) * 1.021
            } else if (kazeiIncome >= 195 && kazeiIncome < 330) {
                sTax = (kazeiIncome * 0.1 - 9.75) * 1.021
            } else if (kazeiIncome >= 330 && kazeiIncome < 695) {
                sTax = (kazeiIncome * 0.2 - 42.75) * 1.021
            } else if (kazeiIncome >= 695 && kazeiIncome < 900) {
                sTax = (kazeiIncome * 0.23 - 63.6) * 1.021
            } else if (kazeiIncome >= 900 && kazeiIncome < 1800) {
                sTax = (kazeiIncome * 0.33 - 153.6) * 1.021
            } else if (kazeiIncome >= 1800 && kazeiIncome < 4000) {
                sTax = (kazeiIncome * 0.4 - 279.6) * 1.021
            } else {
                sTax = (kazeiIncome * 0.45 - 479.6) * 1.021
            }
            let jTax = kazeiIncome2 * 0.1//<=住民税の設定
            let taxSum = sTax + jTax //<=所得税と住民税の合計の設定
            let tedori = taxIncome - shakaiHoken - taxSum//<=手取りの設定

            console.log([
                { taxIncome: taxIncome, kyuyoIncome: kyuyoIncome, tedori: tedori },//<=[0]
                { kisoKojo: kisoKojo, shakaiHoken: shakaiHoken, kazeiIncome: kazeiIncome },//<=[1]
                {
                    shakaiHoken: shakaiHoken,//<=[2]
                    seimeiHoken: Number(seimeiHoken),
                    jisinKojo: 0,
                    wifeKojo: wifeKojo,
                    huyoKojoSum1: huyoKojoSum1,
                    kisoKojo: kisoKojo
                },
                {
                    shakaiHoken: shakaiHoken,//<=[3]
                    seimeiHokenJ: seimeiHokenJ,
                    jisinKojoJ: 0,
                    wifeKojoJ: wifeKojoJ,
                    kisoKojoJ: kisoKojoJ
                }
            ])
            return (
                /*情報の詰まったオブジェクト配列を返す 手取りは　○○[0].tedoriでとりだせるよん！*/
                [
                    { taxIncome: Number(taxIncome), kyuyoIncome: kyuyoIncome, tedori: tedori },//<=[0]
                    { kisoKojo: kisoKojo, huyoKojoSum2: huyoKojoSum2, shakaiHoken: shakaiHoken, kazeiIncome },//<=[1]
                    {
                        shakaiHoken: shakaiHoken,//<=[2]
                        seimeiHoken: seimeiHoken,
                        jisinKojo: 0,
                        wifeKojo: wifeKojo,
                        huyoKojoSum1: huyoKojoSum1,
                        kisoKojo: kisoKojo
                    },
                    {
                        shakaiHoken: shakaiHoken,//<=[3]
                        seimeiHokenJ: seimeiHokenJ,
                        jisinKojoJ: 0,
                        wifeKojoJ: wifeKojoJ,
                        kisoKojoJ: kisoKojoJ
                    }
                ]
            )
        }
    };



    /*参考計算に必要な４つの情報を入手する */
    const getAllInfo = () => {
        setCurrentInfo(dicisionInfo(taxIncome, taxIncome60, pension, huyoList, 0, age, 0))//<=参考計算で必要な情報を万能関数から得る
        setInfo({ age: age, taxIncome: taxIncome, taxIncome60: taxIncome60, pension: pension })
        setWifeInfo({ age: wifeAge, taxIncome: wifeTaxIncome, taxIncome60: wifeIncome60, pension: wifePension })
    }

    /*扶養情報のための関数 */
    const addHuyoInput = () => {
        if (numberH.length === 0) {
            setNumberH([1])
            console.log(1)
        } else {
            console.log(
                Math.max.apply(null, numberH) + 1
            )
            let newNumber = Math.max.apply(null, numberH) + 1
            setNumberH([...numberH, newNumber])
        }

    }

    const addHuyoList = (number, type, childAge, numberOfHuyo, self) => {
        setHuyoList([...huyoList, { id: number, type: type, childAge: childAge, numberOfHuyo: numberOfHuyo, self: self }])
    }

    const deleteHuyoInput = (num) => {
        setNumberH(numberH.map((value) => {
            if (value === num) {
                return (null)
            } else {
                return (value)
            }
        }))
        setHuyoList(huyoList.map((value) => {
            if (value.id === num) {
                return ({ id: 0, type: "none", self: 3 })
            } else {
                return (value)
            }
        }))
    }

    /*支出情報のための関数 */

    const addPayInput = () => {
        if (numberP.length === 0) {
            setNumberP([1])
            console.log(1)
        } else {
            console.log(
                Math.max.apply(null, numberP) + 1
            )
            let newNumber = Math.max.apply(null, numberP) + 1
            setNumberP([...numberP, newNumber])
        }

    }

    const addPayList = (number, type, charge, count, type2) => {
        setPayList([...payList, { id: number, type: type, charge: charge, count: count, type2: type2 }])
    }

    const deletePayInput = (num) => {
        setNumberP(numberP.map((value) => {
            if (value === num) {
                return (null)
            } else {
                return (value)
            }
        }))
        setPayList(payList.map((value) => {
            if (value.id === num) {
                return ({ id: 0, type: "none", charge: 0 })
            } else {
                return (value)
            }
        }))
    }

    /*その他収入情報のための関数 */
    const addIncomeInput = () => {
        if (numberI.length === 0) {
            setNumberI([1])
            console.log(1)
        } else {
            console.log(
                Math.max.apply(null, numberI) + 1
            )
            let newNumber = Math.max.apply(null, numberI) + 1
            setNumberI([...numberI, newNumber])
        }

    }

    const addIncomeList = (number, type, charge, count, type2) => {
        setIncomeList([...incomeList, { id: number, type: type, charge: charge, count: count, type2: type2 }])
    }

    const deleteIncomeInput = (num) => {
        setNumberI(numberI.map((value) => {
            if (value === num) {
                return (null)
            } else {
                return (value)
            }
        }))
        setIncomeList(incomeList.map((value) => {
            if (value.id === num) {
                return ({ id: 0, type: "none", charge: 0 })
            } else {
                return (value)
            }
        }))
    }

    const pages = ['input1', 'caluculation1', 'transition']
    /*ページの分岐 */
    let page;
    if (currentPage === "input1") {//<=入力１の画面
        page = <div className="contents">
            <FormInput label="年齢" type="number" value={age} onChange={(e) => { setAge(e.target.value) }} />
            <br />
            <FormInput label="税込年収" type="number" value={taxIncome} onChange={(e) => { setTaxIncome(e.target.value) }} />
            <br />
            <FormInput label="60~65歳の年収" type="number" value={taxIncome60} onChange={(e) => { setTaxIncome60(e.target.value) }} />
            <br />
            <FormInput label="年金" type="number" value={pension} onChange={(e) => { setPension(e.target.value) }} />
            <br />
            <FormInput label="現在貯蓄額" type="number" value={saving} onChange={(e) => { setSaving(e.target.value) }} />
            <br />
            <label>配偶者の有無</label>
            <div className="buttons">
                <button className="btn btn-border" onClick={() => { setHasWife(1) }}>いる</button>
                <button className="btn btn-border" onClick={() => { setHasWife(0) }}>いない</button>
            </div>
            {hasWife === 1 ?
                <>
                    <FormInput label="配偶者の年齢" type="number" value={wifeAge} onChange={(e) => { setWifeAge(e.target.value) }} />
                    <br />
                    <FormInput label="配偶者の年収" type="number" value={wifeTaxIncome} onChange={(e) => { setWifeTaxIncome(e.target.value) }} />
                    <br />
                    <FormInput label="配偶者の60~65歳の年収" type="number" value={wifeIncome60} onChange={(e) => { setWifeIncome60(e.target.value) }} />
                    <br />
                    <FormInput label="配偶者の年金" type="number" value={wifePension} onChange={(e) => { setWifePension(e.target.value) }} />
                    <br />
                </> : null}


            <label className="button-label">扶養控除
                <button className="btn btn-border" onClick={addHuyoInput}>追加</button>
            </label>
            {numberH.map((value, index) => {
                return (
                    <InputHuyo key={index} number={value} deleteHuyoInput={deleteHuyoInput}
                        addHuyoList={addHuyoList} />
                )
            }
            )}

            <label className="button-label">支出
                <button className="btn btn-border" onClick={addPayInput}>追加</button>
            </label>
            {numberP.map((value, index) => {
                return (
                    <InputPay key={index} number={value} deletePayInput={deletePayInput}
                        addPayList={addPayList} />
                )
            }
            )}

            <label className="button-label">その他収入
                <button className="btn btn-border" onClick={addIncomeInput}>追加</button>
            </label>
            <br />
            {numberI.map((value, index) => {
                return (
                    <InputIncome key={index} number={value} deleteIncomeInput={deleteIncomeInput}
                        addIncomeList={addIncomeList} />
                )
            }
            )}
            <button className="btn btn--yellow" onClick={getAllInfo}>確定</button>
            <br />
            <label>基礎控除</label>
            <p>{currentInfo[1].kisoKojo}</p>
            <label>扶養控除合計額</label>
            <p>{currentInfo[1].huyoKojoSum2}</p>
            <label>給与所得</label>
            <p>{currentInfo[0].kyuyoIncome}</p>
            <label>社会保険</label>
            <p>{currentInfo[1].shakaiHoken}</p>
            <label>課税所得</label>
            <p>{currentInfo[1].kazeiIncome}</p>


        </div>
    } else if (currentPage === "caluculation1") {//<=参考計算例
        page = <div className="contents">
            <Caluculation1 title="参考計算例" info={currentInfo} />
        </div>
    } else {//<=推移画面
        page = <Transition info={info} wifeInfo={wifeInfo} saving={saving} huyoList={huyoList}
            payList={payList} incomeList={incomeList} dicisionInfo={dicisionInfo} />
    }

    /*currentPageのステートによって何の画面が表示されるか決まる */
    return (
        <div className="all-wrapper">
            <h2 className="title">ライフシミュレーション</h2>
            {page}
            <div className="button-wrapper">
                {currentPage !== 'input1' ?
                    <button className="btn btn--yellow" onClick={() => { setCurrentPage(pages[pages.indexOf(currentPage) - 1]) }}>
                        <i class="fas fa-chevron-left"></i>
                        戻る
                    </button> : null}
                {currentPage !== 'transition' ? <button className="btn btn--yellow" onClick={() => { setCurrentPage(pages[pages.indexOf(currentPage) + 1]) }}>
                    次へ
                    <i class="fas fa-chevron-right"></i>
                </button> : null}

            </div>
        </div>
    )
}

export default ApplicationHome;