import { useState,useEffect } from "react"

const Caluculation1=(props)=>{
    const {info,title}=props//<=propsとして結果を持ってきている
    const [realEstate,setRealEstate]=useState(0)
    const [wifeSpecial,setWifeSpecial]=useState(0)
    const [otherKojo,setOtherKojo]=useState(0)
    const [sKojo,setSKojo]=useState(0)
    const [jKojo,setJKojo]=useState(0)
    const [kazeiIncomeS1,setKazeiIncomeS1]=useState(0)
    const [kazeiIncomeS2,setKazeiIncomeS2]=useState(0)
    const [kazeiIncomeJ1,setKazeiIncomeJ1]=useState(0)
    const [kazeiIncomeJ2,setKazeiIncomeJ2]=useState(0)
    const [sTax1,setSTax1]=useState(0)
    const [sTax2,setSTax2]=useState(0)
    const [jTax1,setJTax1]=useState(0)
    const [jTax2,setJTax2]=useState(0)
    const [soneki,setSoneki]=useState(0)

    /*控除内容が変更されたときに控除額を出しなおす */
    useEffect(()=>{
        setSKojo(Number(info[2].shakaiHoken)+Number(info[2].seimeiHoken)+Number(info[2].jisinKojo)+
            Number(info[2].wifeKojo)+Number(wifeSpecial)+Number(info[2].huyoKojoSum1)+Number(info[2].kisoKojo)+Number(otherKojo))
        setJKojo(Number(info[3].shakaiHoken)+Number(info[3].seimeiHokenJ)+Number(info[3].jisinKojoJ)+
            Number(info[3].wifeKojoJ)+Number(wifeSpecial)+Number(info[3].kisoKojoJ)+Number(otherKojo))
    },[wifeSpecial,otherKojo,info])

    /*控除額に変更があった場合に課税所得を出しなおす */
    useEffect(()=>{
        setKazeiIncomeS1(info[0].kyuyoIncome-sKojo)
        setKazeiIncomeJ1(info[0].kyuyoIncome-jKojo)
    },[sKojo,jKojo,info[0].kyuyoIncome])

    useEffect(()=>{
        setKazeiIncomeS2(soneki-sKojo)
        setKazeiIncomeJ2(soneki-jKojo)
    },[sKojo,jKojo,soneki])

    /*申告前の税の設定 */
    useEffect(()=>{

        /*所得税 */
        if(kazeiIncomeS1<195){
            setSTax1((kazeiIncomeS1*0.05)*1.021)
        }else if(kazeiIncomeS1>=195 && kazeiIncomeS1<330){
            setSTax1((kazeiIncomeS1*0.1-9.75)*1.021)
        }else if(kazeiIncomeS1>=330 && kazeiIncomeS1<695){
            setSTax1((kazeiIncomeS1*0.2-42.75)*1.021)
        }else if(kazeiIncomeS1>=695 && kazeiIncomeS1<900){
            setSTax1((kazeiIncomeS1*0.23-63.6)*1.021)
        }else if(kazeiIncomeS1>=900 && kazeiIncomeS1<1800){
            setSTax1((kazeiIncomeS1*0.33-153.6)*1.021)
        }else if(kazeiIncomeS1>=1800 && kazeiIncomeS1<4000){
            setSTax1((kazeiIncomeS1*0.4-279.6)*1.021)
        }else{
            setSTax1((kazeiIncomeS1*0.45-479.6)*1.021)
        }

        /*住民税 */
        setJTax1(kazeiIncomeJ1*0.1)
    },[kazeiIncomeS1,kazeiIncomeS2,kazeiIncomeJ1,,kazeiIncomeJ2])

    /*申告後の税の設定 */
    useEffect(()=>{
        /*所得税 */
        if(kazeiIncomeS2<195){
            setSTax2((kazeiIncomeS2*0.05)*1.021)
        }else if(kazeiIncomeS2>=195 && kazeiIncomeS2<330){
            setSTax2((kazeiIncomeS2*0.1-9.75)*1.021)
        }else if(kazeiIncomeS2>=330 && kazeiIncomeS2<695){
            setSTax2((kazeiIncomeS2*0.2-42.75)*1.021)
        }else if(kazeiIncomeS2>=695 && kazeiIncomeS2<900){
            setSTax2((kazeiIncomeS2*0.23-63.6)*1.021)
        }else if(kazeiIncomeS2>=900 && kazeiIncomeS2<1800){
            setSTax2((kazeiIncomeS2*0.33-153.6)*1.021)
        }else if(kazeiIncomeS2>=1800 && kazeiIncomeS2<4000){
            setSTax2((kazeiIncomeS2*0.4-279.6)*1.021)
        }else{
            setSTax2((kazeiIncomeS2*0.45-479.6)*1.021)
        }

        /*住民税 */
        setJTax2(kazeiIncomeJ2*0.1)
    })


    /*不動産所得が入力されたときに損益通算を出す */
    useEffect(()=>{
        setSoneki(Number(info[0].kyuyoIncome)+Number(realEstate))
    },[realEstate])
    return(
        <>
        <h1>{title}</h1>
        <label>給与収入</label>
        <p>{info[0].taxIncome}</p>
        <label>給与所得</label>
        <p>{info[0].kyuyoIncome}</p>

        <label>不動産所得</label>
        <input value={realEstate} onChange={(e)=>{setRealEstate(e.target.value)}} />
        <br/>
        <label>損益通算</label>
        <p>{Number(info[0].kyuyoIncome)+Number(realEstate)}</p>
        <label>所得税控除</label>
        <table>
            <tbody>
            <tr>
                <td>社会保険控除</td>
                <td>{info[2].shakaiHoken}</td>
            </tr>
            <tr>
                <td>生命保険控除</td>
                <td>{info[2].seimeiHoken}</td>
            </tr>
            <tr>
                <td>地震保険控除</td>
                <td>{info[2].jisinKojo}</td>
            </tr>
            <tr>
                <td>配偶者控除</td>
                <td>{info[2].wifeKojo}</td>
            </tr>
            <tr>
                <td>配偶者特別控除</td>
                <td>{wifeSpecial}</td>
            </tr>
            <tr>
                <td>扶養控除</td>
                <td>{info[2].huyoKojoSum1}</td>
            </tr>
            <tr>
                <td>基礎控除</td>
                <td>{info[2].kisoKojo}</td>
            </tr>
            <tr>
                <td>その他控除</td>
                <td>{otherKojo}</td>
            </tr>
            <tr>
                <td>計</td>
                <td>{sKojo}</td>
            </tr>
            </tbody>
        </table>
        <label>住民税控除</label>
        <table>
            <tbody>
            <tr>
                <td>社会保険控除</td>
                <td>{info[3].shakaiHoken}</td>
            </tr>
            <tr>
                <td>生命保険控除</td>
                <td>{info[3].seimeiHokenJ}</td>
            </tr>
            <tr>
                <td>地震保険控除</td>
                <td>{info[3].jisinKojoJ}</td>
                
            </tr>
            <tr>
                <td>配偶者控除</td>
                <td>{info[3].wifeKojoJ}</td>

                
            </tr>
            <tr>
                <td>配偶者特別控除</td>
                <td>{wifeSpecial}</td>
            </tr>
            <tr>
                <td>扶養控除</td>
                <td>なし</td>
            </tr>
            <tr>
                <td>基礎控除</td>
                <td>{info[3].kisoKojoJ}</td>
            </tr>
            <tr>
                <td>その他控除</td>
                <td>{otherKojo}</td>
            </tr>
            <tr>
                <td>計</td>
                <td>{jKojo}</td>
            </tr>
            </tbody>
        </table>
        
        <label>★申告前の税額</label>
        <br/>
        <label>１ー２⇒</label>
        <label>{kazeiIncomeS1}</label>
        <label>、所得税⇒</label>
        <label>{sTax1}</label>
        <br/>
        <label>１－３⇒</label>
        <label>{kazeiIncomeJ1}</label>
        <label>、住民税⇒</label>
        <label>{jTax1}</label>
        <br/>
        <p>合計{sTax1+jTax1}</p>
        <br/>
        <label>★申告後の税額</label>
        <br/>
        <label>Aー２⇒</label>
        <label>{kazeiIncomeS2}</label>
        <label>、所得税⇒</label>
        <label>{sTax2}</label>
        <br/>
        <label>A－３⇒</label>
        <label>{kazeiIncomeJ2}</label>
        <label>、住民税⇒</label>
        <label>{jTax2}</label>
        <br/>
        <p>合計{sTax2+jTax2}</p>


        <h1>減税額4-5</h1>
        <lavel>所得税還付金</lavel>
        <p>{Number(sTax1)-Number(sTax2)}</p>
        <br/>
        <lavel>住民税減税額</lavel>
        <p>{Number(jTax1)-Number(jTax2)}</p>
        <br/>
        <label>年間節約税額</label>
        <p>{Number(sTax1)-Number(sTax2)+Number(jTax1)-Number(jTax2)}</p>
        </>
    )
}
export default Caluculation1
