import { useState, memo } from "react"
import './Select_module.css'
import { FormInput } from "./FormInput"

const InputPay = memo((props) => {
    const { number, deletePayInput, addPayList } = props
    const [open, setOpen] = useState(true)
    const [isInputed, setIsInputed] = useState(false)
    const [type, setType] = useState("house")
    const [charge, setCharge] = useState('')
    const [count, setCount] = useState('')
    const [type2, setType2] = useState("every")


    const decisionThisPay = () => {
        setIsInputed(true)
        addPayList(number, type, charge, count, type2)
    }

    const deleteThisPay = () => {
        setOpen(false)
        deletePayInput(number)
    }

    let typeLabel1;
    if (type === "house") {
        typeLabel1 = <span>住宅ローン、家賃</span>
    } else if (type === "car") {
        typeLabel1 = <span>車（ローン）</span>
    } else if (type === "nomal") {
        typeLabel1 = <span>一般支出</span>
    } else if (type === "sisanTax") {
        typeLabel1 = <span>固定資産税</span>
    } else if (type === "seimeiHoken") {
        typeLabel1 = <span>生命保険</span>
    } else {
        typeLabel1 = <span>その他</span>
    }

    let typeLabel2;
    if (type2 === "every") {
        typeLabel2 = <span>毎年</span>
    } else if (type2 === "before") {
        typeLabel2 = <span>まで毎年</span>
    }

    return (
        <>
            {open ?
                <div className="option-wrapper">
                    {isInputed ?
                        <>
                            <br />
                            <label>種類 : {typeLabel1}</label>
                            <br />
                            <label>金額（年額） : {charge}万円</label>
                            <br />
                            {type2 === "every" ?
                                <label>回数 : {typeLabel2}</label> :
                                <label>回数 : {count}歳{typeLabel2}</label>
                            }
                            <button className="btn btn-border" onClick={deleteThisPay}>削除</button>
                        </>
                        :
                        <>
                            <br />
                            <div class="cp_ipselect">
                                <select class="cp_sl06" required onChange={(e) => { setType(e.target.value) }}>
                                    <option value="" hidden disabled selected></option>
                                    <option value="house">住宅ローン、家賃</option>
                                    <option value="car">車（ローン）</option>
                                    <option value="nomal">一般支出</option>
                                    <option value="sisanTax">固定資産税</option>
                                    <option value="seimeiHoken">生命保険</option>
                                    <option value="other">その他</option>
                                </select>
                                <span class="cp_sl06_highlight"></span>
                                <span class="cp_sl06_selectbar"></span>
                                <label class="cp_sl06_selectlabel">種類</label>
                            </div>
                            <br />
                            <FormInput label="金額（年額）" type="number" value={charge} onChange={(e) => { setCharge(e.target.value) }} />
                            <br />
                            {type2 === "before" ?
                                <>
                                    <FormInput label="年齢" type="number" value={count} onChange={(e) => { setCount(e.target.value) }} />
                                    <br />
                                </>
                                : null}
                            <div class="cp_ipselect">
                                <select class="cp_sl06" required onChange={(e) => { setType2(e.target.value) }}>
                                    <option value="" hidden disabled selected></option>
                                    <option value="every">毎年</option>
                                    <option value="before">歳まで毎年</option>
                                </select>
                                <span class="cp_sl06_highlight"></span>
                                <span class="cp_sl06_selectbar"></span>
                                <label class="cp_sl06_selectlabel">毎年</label>
                            </div>


                            <div className="button-wrapper">
                                <button className="btn btn-border" onClick={decisionThisPay}>決定</button>
                                <button className="btn btn-border" onClick={deleteThisPay}>削除</button>
                            </div>
                        </>
                    }

                </div>
                : null}

        </>
    )
})

export default InputPay