import { useState, memo } from "react"
import './Select_module.css'
import { FormInput } from "./FormInput"

const InputHuyo = memo((props) => {
    const { number, deleteHuyoInput, addHuyoList } = props
    const [open, setOpen] = useState(true)
    const [isInputed, setIsInputed] = useState(false)
    const [type, setType] = useState("wife")
    const [childAge, setChildAge] = useState('')
    const [numberOfHuyo, setNumberOfHuyo] = useState('')
    const [self, setSelf] = useState('')


    const decisionThisHuyo = () => {
        setIsInputed(true)
        addHuyoList(number, type, childAge, numberOfHuyo, Number(self))
    }

    const deleteThisHuyo = () => {
        setOpen(false)
        deleteHuyoInput(number)
    }

    let typeLabel;
    if (type === "child") {
        typeLabel = <span> 扶養~22歳</span>
    } else if (type === "wife") {
        typeLabel = <span> 配偶者</span>
    } else if (type === "other") {
        typeLabel = <span> その他扶養</span>
    } else if (type === "sinior1") {
        typeLabel = <span> 老人同居</span>
    } else {
        typeLabel = <span> 老人別居</span>
    }
    return (
        <div>
            {open ?
                <div className="option-wrapper">
                    {isInputed ?
                        <>
                            <br />
                            <label>種類 :
                                {typeLabel}
                                <br />
                                <br />
                                {type === "child" ?
                                    <>
                                        <label>年齢 : {childAge}歳</label>
                                    </>
                                    :
                                    <>
                                        <label>人数 : {numberOfHuyo}人</label>
                                    </>
                                }
                                <br />
                            </label>

                            <label>対象 :
                                {self ?
                                    <span> 本人</span>
                                    :
                                    <span> 配偶者</span>}

                                <br />
                            </label>
                            <button className="btn btn-border" onClick={deleteThisHuyo}>削除</button>
                        </>
                        :
                        <>
                            <br />
                            <div class="cp_ipselect">
                                <select class="cp_sl06" required onChange={(e) => { setType(e.target.value) }}>
                                    <option value="" hidden disabled selected></option>
                                    <option value="wife">配偶者</option>
                                    <option value="child">扶養~22</option>
                                    <option value="other">扶養その他</option>
                                    <option value="senior1">老人(70歳以上)同居</option>
                                    <option value="senior2">老人(70歳以上)別居</option>
                                </select>
                                <span class="cp_sl06_highlight"></span>
                                <span class="cp_sl06_selectbar"></span>
                                <label class="cp_sl06_selectlabel">種類</label>
                            </div>
                            <br />

                            {type === "child" ?
                                <>
                                    <FormInput label="年齢" type="number" value={childAge} onChange={(e) => { setChildAge(e.target.value) }} />
                                </>
                                :
                                <>
                                    <FormInput label="人数" type="number" value={numberOfHuyo} onChange={(e) => { setNumberOfHuyo(e.target.value) }} />
                                </>
                            }

                            <br />
                            <div class="cp_ipselect">
                                <select class="cp_sl06" required onChange={(e) => { setSelf(e.target.value) }}>
                                    <option value="" hidden disabled selected></option>
                                    <option value="0">本人</option>
                                    <option value="1">配偶者</option>
                                </select>
                                <span class="cp_sl06_highlight"></span>
                                <span class="cp_sl06_selectbar"></span>
                                <label class="cp_sl06_selectlabel">対象</label>
                            </div>
                            <br />

                            <div className="button-wrapper">
                                <button className="btn btn-border" onClick={decisionThisHuyo}>決定</button>
                                <button className="btn btn-border" onClick={deleteThisHuyo}>削除</button>
                            </div>
                        </>
                    }

                </div>
                : null}
        </div>
    )
})

export default InputHuyo