import { useEffect } from "react"
import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';




function Transition(props) {
    const { info, wifeInfo, saving, huyoList, payList, incomeList, dicisionInfo } = props //<=これが推移に必要な道具
    //順番に その人の情報、配偶者情報、現在の貯蓄、扶養リスト、支出リスト、その他収入リスト、万能関数
    //万能関数の引数に適切な値を突っ込めばその年の手取りが出るようになってる

    /* ？歳までを選択した際の期限が切れてるかを確かめて0か1を返す関数 */
    function isExpired(type2, count, age) {
        if (type2 === "every") {
            return 1;
        }
        else {
            if (age <= count) {
                return 1;
            }
            else {
                return 0;
            }
        }
    }

    /* 配列の中身を合計する（？歳までを選択した場合はそこからは足さない） */
    const sumArray = (array, age) => {
        let sum = 0;
        for (let i = 0, len = array.length; i < len; i++) {
            sum += (Number)(array[i].charge) *
                isExpired(array[i].type2, (Number)(array[i].count), age);
        }
        return sum;
    };

    var data = [];

    //const dicisionInfo = (taxIncome1, taxIncome60, pension, huyoList, who, age, year)
    useEffect(() => {


        /**初期値を計算 */

        var i = 0
        let outCome = sumArray(payList, (Number)(info.age) + i)
        let inCome =
            Number(dicisionInfo(info.taxIncome, info.taxIncome60, info.pension, huyoList, 0, info.age, i)[0].tedori) +
            Number(dicisionInfo(wifeInfo.taxIncome, wifeInfo.taxIncome60, wifeInfo.pension, huyoList, 1, wifeInfo.age, i)[0].tedori) +
            sumArray(incomeList, (Number)(info.age) + i)
        let Result = inCome - outCome

        data[0] = {
            name: info.age + "歳",
            expenses: Number(saving) + Result
        }



        for (let i = 1; i < 110 - info.age; i++) {

            outCome = sumArray(payList, (Number)(info.age) + i)

            inCome =
                Number(dicisionInfo(info.taxIncome, info.taxIncome60, info.pension, huyoList, 0, info.age, i)[0].tedori) +
                Number(dicisionInfo(wifeInfo.taxIncome, wifeInfo.taxIncome60, wifeInfo.pension, huyoList, 0, wifeInfo.age, i)[0].tedori) +
                sumArray(incomeList, (Number)(info.age) + i)

            Result = inCome - outCome

            data.push(
                {
                    name: (Number)(info.age) + i + "歳",
                    expenses: Result + Number(data[i - 1].expenses)
                    //Number(data[i - 1].expenses
                    //dicisionInfo(info.taxIncome, info.taxIncome60, info.pension, huyoList, 0, info.age, i)[0].tedori,
                }
            )
            //console.log(dicisionInfo(info.taxIncome, info.taxIncome60, info.pension, huyoList, 0, info.age, i)[0].tedori)
            //console.log(sumArray(payList, (Number)(info.age) + i));
        }
    }, [])

    return (
        <ResponsiveContainer width="100%" height="100%">
            <BarChart
                width={500}
                height={300}
                data={data}
                margin={{
                    top: 20,
                    right: 30,
                    left: 20,
                    bottom: 5,
                }}
            >
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Bar dataKey="expenses" stackId="a" fill="#FFA500" />
            </BarChart>
        </ResponsiveContainer>

    )
}

export default Transition